
"use client";

import { useState, useMemo, useEffect } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Loader2, Shuffle, CheckCircle, XCircle, Trophy, Save, ArrowRight } from "lucide-react";
import { cn } from "@/lib/utils";
import type { GenerateEducationalGameOutput } from "@/ai/flows/educational-game-generation";
import { useToast } from "@/hooks/use-toast";
import { Input } from "@/components/ui/input";
import { Progress } from "@/components/ui/progress";

interface GameClientProps {
  initialGameData: GenerateEducationalGameOutput | null;
  initialGrade?: number;
  initialTopic?: string;
  initialGameType?: string;
}

type Term = { word: string; definition: string };

const gameTypes = [
  { id: 'Quiz', name: 'Quiz', description: 'Multiple choice questions' },
  { id: 'Bingo', name: 'Bingo Cards', description: 'Educational bingo with terms' },
  { id: 'True/False', name: 'True/False Quiz', description: 'True or false statements' },
  { id: 'Fill-in-the-blanks', name: 'Fill in the Blanks', description: 'Complete sentences with missing words' },
  { id: 'Quick Draw', name: 'Quick Draw', description: 'Draw and guess concepts' },
  { id: 'Word Association', name: 'Word Association', description: 'Related words to topic' },
  { id: 'Sorting', name: 'Sorting Game', description: 'Categorize items into groups' },
  { id: 'Riddles', name: 'Riddles', description: 'Educational riddles with answers' },
  { id: 'Scrambled Words', name: 'Scrambled Words', description: 'Unscramble key terms' },
  { id: 'Odd One Out', name: 'Odd One Out', description: 'Find which item doesn\'t belong' },
  { id: 'Quick Questions', name: 'Quick Questions', description: 'Rapid-fire yes/no questions' }
];

function shuffleArray<T>(array: T[]): T[] {
  const newArray = [...array];
  for (let i = newArray.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [newArray[i], newArray[j]] = [newArray[j], newArray[i]];
  }
  return newArray;
}

const colorClasses = [
  "bg-chart-1/20 border-chart-1 text-foreground hover:bg-chart-1/30",
  "bg-chart-2/20 border-chart-2 text-foreground hover:bg-chart-2/30",
  "bg-chart-3/20 border-chart-3 text-foreground hover:bg-chart-3/30",
  "bg-chart-4/20 border-chart-4 text-foreground hover:bg-chart-4/30",
  "bg-chart-5/20 border-chart-5 text-foreground hover:bg-chart-5/30",
];

const QuizDisplay = ({ gameData }: { gameData: any }) => {
    const { questions } = gameData.content;
    const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
    const [selectedAnswer, setSelectedAnswer] = useState<string | null>(null);
    const [isAnswered, setIsAnswered] = useState(false);
    const [score, setScore] = useState(0);

    const currentQuestion = questions[currentQuestionIndex];
    const isCorrect = selectedAnswer === currentQuestion.answer;

    useEffect(() => {
        // Reset state when game data changes
        setCurrentQuestionIndex(0);
        setSelectedAnswer(null);
        setIsAnswered(false);
        setScore(0);
    }, [gameData]);

    const handleAnswerSelect = (option: string) => {
        if (isAnswered) return;
        setSelectedAnswer(option);
        setIsAnswered(true);
        if (option === currentQuestion.answer) {
            setScore(prev => prev + 1);
        }
    };

    const handleNextQuestion = () => {
        setIsAnswered(false);
        setSelectedAnswer(null);
        setCurrentQuestionIndex(prev => prev + 1);
    };

    const isQuizOver = currentQuestionIndex >= questions.length;

    if (isQuizOver) {
        return (
            <div className="flex flex-col items-center justify-center gap-4 text-center p-8 bg-accent/20 rounded-lg">
                <Trophy className="w-16 h-16 text-yellow-400" />
                <h2 className="text-2xl font-bold font-headline">Quiz Complete!</h2>
                <p className="text-muted-foreground text-lg">You scored {score} out of {questions.length}.</p>
                <Button onClick={() => setCurrentQuestionIndex(0)}><Shuffle className="mr-2 h-4 w-4"/> Play Again</Button>
            </div>
        );
    }

    return (
        <div className="space-y-6">
            <div className="space-y-2">
                <Progress value={((currentQuestionIndex + 1) / questions.length) * 100} className="h-2" />
                <p className="text-sm text-muted-foreground text-center">Question {currentQuestionIndex + 1} of {questions.length}</p>
            </div>
            <Card className="bg-muted/50">
                <CardHeader>
                    <CardTitle className="font-headline text-xl">{currentQuestion.question}</CardTitle>
                </CardHeader>
                <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    {currentQuestion.options.map((option: string) => {
                        const isSelected = selectedAnswer === option;
                        const isCorrectAnswer = currentQuestion.answer === option;
                        return (
                            <Button
                                key={option}
                                variant="outline"
                                className={cn(
                                    "w-full justify-start text-left whitespace-normal min-h-[4rem]",
                                    isAnswered && isCorrectAnswer && "bg-green-500/20 border-green-500 text-foreground",
                                    isAnswered && isSelected && !isCorrectAnswer && "bg-red-500/20 border-red-500 text-foreground",
                                    !isAnswered && isSelected && "ring-2 ring-accent"
                                )}
                                onClick={() => handleAnswerSelect(option)}
                                disabled={isAnswered}
                            >
                                {isAnswered && isCorrectAnswer && <CheckCircle className="mr-2 h-4 w-4"/>}
                                {isAnswered && isSelected && !isCorrectAnswer && <XCircle className="mr-2 h-4 w-4"/>}
                                {option}
                            </Button>
                        );
                    })}
                </CardContent>
            </Card>
            <div className="flex justify-end">
                {isAnswered && (
                     <Button onClick={handleNextQuestion}>
                         {currentQuestionIndex === questions.length - 1 ? 'Finish Quiz' : 'Next Question'}
                         <ArrowRight className="ml-2 h-4 w-4"/>
                     </Button>
                )}
            </div>
        </div>
    );
};

const GenericGameDisplay = ({ gameData, gameType }: { gameData: any, gameType: string }) => {
     return (
        <div>
            <h3 className="text-lg font-bold font-headline mb-2">Developer Preview: {gameType}</h3>
            <p className="text-sm text-muted-foreground mb-4">This is the raw data generated by the AI. A custom component needs to be built to render this game type.</p>
            <pre className="whitespace-pre-wrap font-body text-sm leading-relaxed bg-muted p-4 rounded-md">{JSON.stringify(gameData.content, null, 2)}</pre>
        </div>
     );
}

export function GameClient({ initialGameData, initialGrade, initialTopic, initialGameType }: GameClientProps) {
  const router = useRouter();
  const searchParams = useSearchParams();
  const { toast } = useToast();
  const [pending, setPending] = useState(false);
  const [grade, setGrade] = useState<string>(initialGrade?.toString() ?? "");
  const [topic, setTopic] = useState<string>(initialTopic ?? "");
  const [gameType, setGameType] = useState<string>(initialGameType ?? "");
  
  const [gameData, setGameData] = useState(initialGameData);

  useEffect(() => {
    if (initialGameData) {
      setPending(false);
    }
    setGameData(initialGameData);
  }, [initialGameData]);

  const handleGenerate = () => {
    if (grade && topic && gameType) {
      setPending(true);
      const params = new URLSearchParams();
      params.set('grade', grade);
      params.set('topic', topic);
      params.set('gameType', gameType);
      router.push(`/games?${params.toString()}`);
    }
  };
  
  const handleSave = () => {
    toast({ title: "Saved to My Library (Not Implemented)" });
  };
  
  const renderGameContent = () => {
    if (!gameData) return null;

    const game = gameData.game;

    switch (game.type.toLowerCase()) {
        case 'quiz':
            return <QuizDisplay gameData={game} />;
        case 'bingo':
        case 'true/false':
        case 'fill-in-the-blanks':
        case 'quick draw':
        case 'word association':
        case 'sorting':
        case 'riddles':
        case 'scrambled words':
        case 'odd one out':
        case 'quick questions':
            return <GenericGameDisplay gameData={game} gameType={game.type} />;
        default:
            return <GenericGameDisplay gameData={game} gameType={`Unknown (${game.type})`} />;
    }
  }

  return (
    <>
      <Card>
        <CardHeader>
          <CardTitle className="font-headline">Game Setup</CardTitle>
          <CardDescription>Select a grade, topic, and game type to generate a new educational game.</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex flex-col sm:flex-row items-end gap-2">
             <div className="grid w-full max-w-xs gap-1.5">
              <label htmlFor="grade-select">Target Grade Level</label>
              <Select value={grade} onValueChange={setGrade}>
                <SelectTrigger id="grade-select">
                  <SelectValue placeholder="Select Grade" />
                </SelectTrigger>
                <SelectContent>
                  {[...Array(10)].map((_, i) => (
                    <SelectItem key={i + 1} value={(i + 1).toString()}>Grade {i + 1}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
             <div className="grid w-full max-w-xs gap-1.5">
               <label htmlFor="topic-input">Topic</label>
               <Input id="topic-input" placeholder="e.g., Animals, Space" value={topic} onChange={(e) => setTopic(e.target.value)} />
            </div>
             <div className="grid w-full max-w-xs gap-1.5">
                <label htmlFor="game-type-select">Game Type</label>
                <Select value={gameType} onValueChange={setGameType}>
                    <SelectTrigger id="game-type-select">
                        <SelectValue placeholder="Select Game Type" />
                    </SelectTrigger>
                    <SelectContent>
                        {gameTypes.map((type) => (
                            <SelectItem key={type.id} value={type.id}>{type.name}</SelectItem>
                        ))}
                    </SelectContent>
                </Select>
             </div>
            <Button onClick={handleGenerate} disabled={!grade || !topic || !gameType || pending}>
              {pending && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
              Generate Game
            </Button>
          </div>
        </CardContent>
      </Card>

      {pending && !gameData && (
        <Card>
          <CardHeader>
            <CardTitle className="font-headline flex items-center gap-2">
              <Loader2 className="h-5 w-5 animate-spin text-primary" />
              <span>Creating your game...</span>
            </CardTitle>
          </CardHeader>
           <CardContent>
            <div className="space-y-2">
               <div className="h-8 bg-muted rounded animate-pulse w-full"></div>
               <div className="h-8 bg-muted rounded animate-pulse w-5/6"></div>
            </div>
          </CardContent>
        </Card>
      )}

      {gameData && (
        <Card>
            <CardHeader>
                <div className="flex justify-between items-center">
                    <div>
                        <CardTitle className="font-headline">{gameData.game.title}</CardTitle>
                        <CardDescription>{gameData.game.instructions}</CardDescription>
                    </div>
                </div>
            </CardHeader>
            <CardContent>
               {renderGameContent()}
            </CardContent>
            <CardFooter>
                 <Button variant="outline" onClick={handleSave}><Save className="mr-2" /> Save Game Content</Button>
            </CardFooter>
        </Card>
      )}
    </>
  );
}
